.slider {
  width: 80vw;
  height: auto;
  margin: 2rem auto;
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
  width: 100%;

  background-color: transparent;
  border-radius: 5px;
  box-shadow: 1px 2px 5px 3px rgba(255, 99, 71, 0.4);
}
@media (max-width: 900px) {
  .slider {
    width: 70vw;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    gap: 3rem;
  }
}
